import React, { useContext, useState } from 'react'
import { AuthContext } from '../../context/AuthContext'
import { UiContext } from '../../context/UiContext'
import { actionTypes } from '../../context/UiContextReducer'
import { useInput } from '../../hooks/inputHook'
import { signInAuthWithEmailAndPassword } from '../../util/firebase.utils'
import EmailAuth from './EmailAuth'
import { ErrorMessage } from './EmailAuth.styles'
import { EmailInput, EmailLogInButton, EmailLoginFormContainer, EmailLoginInputContainer } from './EmailLogin.styles'

const EmailLogin = () => {

    const {showLoginWithEmail, UiToggleHelper,showLoginPrompt} = useContext(UiContext)
    const {setAuthHandler,setUserData} = useContext(AuthContext)
    const {input: email, InputHandler:emailHandler} = useInput()
    const {input: password, InputHandler: passwordHandler} = useInput()

    const [errorMessage, setErrorMessage] = useState('')

    const submitHandler = async(event) => {
        event.preventDefault()
      
          try {
            await signInAuthWithEmailAndPassword(email,password).then(({user}) => {
          
              setAuthHandler(user)
              setUserData(user.uid)
              UiToggleHelper(actionTypes.SHOW_LOGIN_WITH_EMAIL,showLoginWithEmail)
            })
            .catch((error)=>{
              switch (error.code) {
                case 'auth/user-not-found':
                  setErrorMessage('No user associated with this email')
                  break;
              case 'auth/wrong-password':
                 setErrorMessage('Wrong Password')
                break
                default:
                   setErrorMessage('There is an error',error.code)
                
                  break;
              }
            })
          
            
           
          } catch (error) {
            
            
          }
    }

    const closeEmailForm = () => {
        UiToggleHelper(actionTypes.SHOW_LOGIN_WITH_EMAIL, showLoginWithEmail)
        UiToggleHelper(actionTypes.SHOW_LOGIN_PROMPT, showLoginPrompt)
    }


  return (
    <EmailAuth show={showLoginWithEmail} close={closeEmailForm} title={'Log in'}>
        <EmailLoginFormContainer onSubmit={submitHandler}>

        <EmailLoginInputContainer>
            <EmailInput onChange={emailHandler} required type='email' placeholder='email address' value={email} name='email' label='Email' />
<EmailInput onChange={passwordHandler} required type='password' placeholder='password' value={password} name='password' label='Password'/>
{errorMessage && <ErrorMessage>{errorMessage}</ErrorMessage> }

            </EmailLoginInputContainer>

            

<EmailLogInButton>Log in</EmailLogInButton>

        </EmailLoginFormContainer>
    </EmailAuth>
  )
}

export default EmailLogin